class Validador {
    static _val = null

    static get instance() {
    if (Validador._val === null) {
        Validador._val = new Validador()
    }
    return Validador._val
    }
    constructor() {

    }

    async ValidadorWhatsApp(data){
        let nuevaData = 0;
        let nuevaBoolean = false;

        if(data === undefined || data === ""){
            nuevaData = 0;
            nuevaBoolean = true;
        } else {
            if(data.length === 10){
                nuevaData = 5;
                nuevaBoolean = false;
            } else {
                nuevaData = 2;
                nuevaBoolean = true;
            }
        }

        let params = {
            data: nuevaData,
            boolean: nuevaBoolean,
        }
        return params;
    }

    async ValidadorCorreo(data){
        let nuevaData = 0;
        let nuevaBoolean = false;
        if(data === undefined || data === ""){
            nuevaData = 0;
            nuevaBoolean = true;
        } else {
            let regexEmail = /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g;
            let email = regexEmail.test(data);
            if(email === true){
                nuevaData = 5;
                nuevaBoolean = false;
            } else {
                nuevaData = 1;
                nuevaBoolean = true;
            }
        }

        let params = {
            data: nuevaData,
            boolean: nuevaBoolean,
        }

        return params;
    }

    async ValidadorVIN(data){
        let nuevaData = 0;
        let nuevaBoolean = false;
        console.log("Entra validador")
        if(data === undefined || data === ""){
            nuevaData = 0;
            nuevaBoolean = true;
        } else {
            if(data.length === 8){
                nuevaData = 5;
                nuevaBoolean = false;
            } else {
                nuevaData = 3;
                nuevaBoolean = true;
            }
        }

        let params = {
            data: nuevaData,
            boolean: nuevaBoolean,
        }
        return params;
    }

    async ValidadorRFC(RFC, Regimen){
        let nuevaData = 0;
        let nuevaBooleanRFC = false;
        let nuevaBooleanRegimen = false;

        if(Regimen === "Selecciona"){
            nuevaData = 8;
            nuevaBooleanRegimen = true;
        } else if (Regimen === "Física") {
            nuevaBooleanRegimen = false;
            if(RFC === "" || RFC === undefined){
                console.log("llega aquí vacio")
                nuevaData = 0;
                nuevaBooleanRFC = true;
            } else {
                console.log("llega aquí tiene valores")
                if(RFC.length === 13){
                    nuevaData = 5;
                    nuevaBooleanRFC = false;
                } else {
                    nuevaData = 9;
                    nuevaBooleanRFC = true;
                }
            }
        } else if (Regimen === "Moral") {
            nuevaBooleanRegimen = false;
            if(RFC === "" || RFC === undefined){
                nuevaData = 0;
                nuevaBooleanRFC = true;
            } else {
                if(RFC.length === 12){
                    nuevaData = 5;
                    nuevaBooleanRFC = false;
                } else {
                    nuevaData = 9;
                    nuevaBooleanRFC = true;
                }
            }
        }

        let params = {
            data: nuevaData,
            booleanRFC: nuevaBooleanRFC,
            booleanRegimen: nuevaBooleanRegimen
        }

        return params;

    }

    async ValidadorGenero(data){
        //console.log("Estamos en el ValidadorGenero: ", data);
        let nuevaData = 0;
        let nuevaBoolean = false;

        if(data === "Selecciona"){
            nuevaData = 8;
            nuevaBoolean = true;
        } else {
            nuevaData = 5;
            nuevaBoolean = false;
        }

        let params = {
            data: nuevaData,
            boolean: nuevaBoolean
        }
        return params;
    }

    async ValidadorNombre(data){
        let nuevaData = 0;
        let nuevaBoolean = false;

        if(data === "" || data === undefined){
            nuevaData = 0;
            nuevaBoolean = true;
        } else {
            nuevaData = 5;
            nuevaBoolean = false;
        }

        let params ={
            data: nuevaData,
            boolean: nuevaBoolean,
        }
        return params
    }

    // Validador de campos tipo formularios
    async ValidadorCamposFacturacion(data){
        let nuevaData = 0;
        //console.log("Recibo lo siguiente aquí", data);

        if(data.Calle === "" || data.Celular === "" || data.Colonia === "" || data.Correo === "" || data.Nombre === "" || data.RFC === "" || data.Telefono === ""|| data.CodigoP === ""){
            nuevaData = 0;
        } else {
            nuevaData = 5;
        }

        return nuevaData;
    }

    async ValidadorCamposNuevoUsuario(data){
        let jijiData = 5;

        let ValNombre = await this.ValidadorNombre(data.Nombre);
        let ValCorreo = await this.ValidadorCorreo(data.Correo);
        let VaCelular = await this.ValidadorWhatsApp(data.Celular);
        let VaRFC = await this.ValidadorRFC(data.RFC, data.Regimen);
        let VaGenero = await this.ValidadorGenero(data.Genero);

        let ValCalle = await this.ValidadorNombre(data.Calle);
        let ValNumero = await this.ValidadorNombre(data.Numero);
        let ValColonia = await this.ValidadorNombre(data.Colonia);
        let ValCPostal = await this.ValidadorNombre(data.CPostal);

        if(ValNombre.data === 0 || ValCorreo.data === 0 || VaCelular.data === 0 || VaRFC.data === 0 || ValCalle.data === 0 || ValNumero.data === 0 || ValColonia.data === 0 || ValCPostal.data === 0){
            jijiData = 0;
        } else {
            if(ValCorreo.data === 1){ jijiData = ValCorreo.data; } 
            else if(VaCelular.data === 2) { jijiData = VaCelular.data; }
            else if(VaRFC.data === 8 || VaRFC.data === 9) { jijiData = VaRFC.data; }
            else if(VaGenero.data === 8) { jijiData = VaGenero.data; }
        } 

        let nuevaData = {
            error: jijiData,
            nombre: ValNombre,
            correo: ValCorreo,
            celular: VaCelular,
            rfc: VaRFC,
            genero: VaGenero,
            calle: ValCalle,
            numero: ValNumero,
            colonia: ValColonia,
            cpostal: ValCPostal
        };

        return nuevaData;
    }

    async ValidadorCamposDatosAutos(data){
        //console.log("Tengo lo siguiente jiji, ", data);
        let ErrorJiji = 0;

        let valColor = await this.ValidadorNombre(data.Color);
        let valAnio = await this.ValidadorNombre(data.Anio);
        let valVIN = await this.ValidadorNombre(data.VINDatos);
        let valPlacas = await this.ValidadorNombre(data.Placas);
        let valKilometraje = await this.ValidadorNombre(data.Kilometraje);

        if(valColor.data === 0 || valAnio.data === 0 || valVIN.data === 0 || valPlacas.data === 0 || valKilometraje.data === 0) {
            ErrorJiji = 0;
        } else {
            ErrorJiji = 5;
        }

        let params = {
            error: ErrorJiji,
            color: valColor,
            anio: valAnio,
            vin: valVIN,
            placas: valPlacas,
            kilometraje: valKilometraje,
        }

        return params;

    }

}

module.exports = Validador.instance