import React, { useState, useRef, useEffect } from "react";
import { Col, Row, Container, Button } from "react-bootstrap";
import { navigate } from "gatsby";
import { TailSpin } from "react-loader-spinner";

import Layout from "../components/layout.js";
import val from "../components/validadores";
import Seo from "../components/seo";
import Keyboards from "../components/Keyboards.js";
import "../components/pages-css/sCita.css";
import VideoSeccion from "../components/videoSeccion.js";
import ModalEmergente from "../components/ModalEmergente.js";
import Timer from "../components/timer.js";
import api from "../components/actions/riveroapi";

export default function OrdenReparacion(){

    const [show, setShow] = useState(false);
    const [modal, setModal] = useState(false);
    const [errorInput, setErrorInput] = useState(false);
    const [error, setError] = useState(0);
    const [preferencia, setPreferencia] = useState("WhatsApp");
    const [click, setClick] = useState(false);
    const [mensajeModal, setMensajeModal] = useState("");
    const [loading, setLoading] = useState(false);
    const refForm = useRef();

    const [optionsKeyboard, setOptionsKeyboard] = useState({
      inpName: "",
      layoName: "phone",
      name: "",
      value: {
        Campo: undefined
      }
    })

    useEffect(() => {
      if (typeof window !== 'undefined') {
          //console.log('we are running on the client');
          setOptionsKeyboard({
            ...optionsKeyboard,
            value: {
              Campo: localStorage.getItem('Campo') || "",
            }
          })
      } else {
          //console.log('we are running on the server');
      }
    }, [])
  
    function cambioPreferencia(data){
      if(data === "WhatsApp"){
        setOptionsKeyboard({
          ...optionsKeyboard,
            layoName: "phone"
        })
        setPreferencia("WhatsApp");
      } else if (data === "Correo"){
        setOptionsKeyboard({
          ...optionsKeyboard,
            layoName: "default"
        })
        setPreferencia("Correo");
      } else if (data === "VIN"){
        setOptionsKeyboard({
          ...optionsKeyboard,
            layoName: "vin"
        })
        setPreferencia("VIN");
      }
  
      document.getElementById("btnWhatsApp").className =
      "btn btn-preferencia"
      document.getElementById("btnCorreo").className =
        "btn btn-preferencia"
      document.getElementById("btnVIN").className =
        "btn btn-preferencia"
      document.getElementById("btn" + data).className =
        "btn btn-preferenciaActivo"
  
    }
  
    function continuar(){ 
      if(preferencia === "WhatsApp"){
        val.ValidadorWhatsApp(optionsKeyboard.value.Campo).then(r => {
          if(r.data === 5){
            localStorage.setItem('Campo', optionsKeyboard.value.Campo);
              buscandoCita("whatsapp");
              setLoading(true);
          } else {
            setModal(true)
            setError(r.data);
            setErrorInput(r.boolean);
            setLoading(false);
          }
        })
      } else if (preferencia === "Correo"){
        val.ValidadorCorreo(optionsKeyboard.value.Campo).then((r) => {
            if(r.data === 5){
              localStorage.setItem('Campo', optionsKeyboard.value.Campo);
              setLoading(true);
              buscandoCita("correo")
            } else {
              setModal(true)
              setError(r.data);
              setLoading(false);
            }
        })
      } else if (preferencia === "VIN"){
        if(optionsKeyboard.value.Campo === "" || optionsKeyboard.value.Campo === undefined) {
          setModal(true)
          setError(0);
          setLoading(false);
        } else {
          //console.log("Le dio en continuar en la sección VIN", optionsKeyboard.value.Campo)
          buscandoCita("vin");
          setLoading(true);
        }
      }
    }

    function buscandoCita(data){

      const params = {
        campo: optionsKeyboard.value.Campo,
        medio: data
      }

      api.getConCita(params).then((resp) => {
        //console.log("Resp", resp);

        if(resp === 5) {
          //console.log("Incorrecto: ", resp)
          setModal(true)
          setError(resp);
          setLoading(false);
          setErrorInput(true);
        } else {
          //console.log("Correcto: ", resp[0])
          setModal(true)
          setError(14);
          setLoading(false);
          var newName = resp[0].client.name.replace(/,/g, " ")
          setMensajeModal(
            <center>
                <div style={{marginBottom: 15}}>{newName}</div>
                <Row className="form-control text-left" style={{margin: 10}} onClick={() => selectCita(resp)}>
                {resp[0].car.model} - {resp[0].car.year}, Color: {resp[0].car.color}, VIN: {resp[0].car.vin}, Placas: {resp[0].car.placas}
                </Row>
            </center>
          )
        }

      })
      .catch((error) => {
        setModal(true)
        setError(4);
        setLoading(false);
      })
    }

    function selectCita(data){
      //console.log(data)  
      //navigate("/servicio-a-realizar");
      navigate("/kilometraje");
      localStorage.setItem("RutaNavegacion", "orden-reparacion");
      setLoading(false);
      localStorage.setItem("informacion", JSON.stringify(data));
    }

    return(
        <Layout backUrl={"/"}>
            <Seo title="Auto servicio" />
              {loading === true ? 
                <Container style={{position: 'absolute', zIndex: 100, height: '80%', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                    <TailSpin
                      height="100"
                      width="100"
                      color="#0d6efd"
                      ariaLabel="tail-spin-loading"
                      radius="0"
                      wrapperStyle={{}}
                      wrapperClass=""
                      visible={true}
                    />
                </Container>
              : null}
            <Container className="container-sin-cita" onClick={() => {setClick(true)}}>
            {<Timer click={click} setClick={setClick} setError={setError} setModal={setModal}/>}
                <Row className="row-cita-arriba">
                    <div className="container-cita">
    
                        <Row>
                        <Col className="titulo-cita"><h1 className="titulo-h1">¿Qué tal?, Muy buen día.</h1></Col>
                        </Row>
                        <Row>
                        <Col className="titulo-cita" style={{flexDirection: 'column'}}>
                            <h4> Buscar mi cuenta</h4>
                            <p className="texto-gris">*¿Cómo quieres buscar?</p>
                        </Col>
                        </Row>
                        <Row style={{display: 'flex', justifyContent: 'center'}}>
                          <Row style={{display: 'flex', justifyContent: 'space-around', width: '70%'}}>
                            <Button className="btn-preferenciaActivo" id="btnWhatsApp" onClick={() => cambioPreferencia("WhatsApp")}>WhatsApp</Button>
                            {/*<Button className="btn-preferencia" id="btnCorreo" onClick={() => cambioPreferencia("Correo")}>Correo</Button>*/}
                            <Button className="btn-preferencia" id="btnVIN" onClick={() => cambioPreferencia("VIN")}>VIN</Button>
                          </Row>  
                        </Row>
    
                        <Row className="row-cita-abajo" style={{marginBottom: 0}}>
                            <Col lg={9}>
                                <input
                                autoComplete="off"
                                type="text"
                                placeholder={`Ingrese su ${preferencia}`}
                                id={errorInput === true ? "inputContactoError" : "inputContacto"}
                                className="form-control"
                                onClick={() => setShow(true)}
                                onFocus={() => setOptionsKeyboard({...optionsKeyboard, inpName: "Campo"})}
                                value={optionsKeyboard.value.Campo || ""}
                                />
                        
                            </Col>
                        </Row>
                        <Row>
                          <Col className="titulo-cita">
                            {preferencia === "WhatsApp" ? 
                              <p className="texto-gris" style={{fontSize: 12}}>Ingresa el teléfono que registraste con nosotros en tu compra o última visita.</p>  
                            : null}
                            {preferencia === "Correo" ? 
                              <p className="texto-gris" style={{fontSize: 12}}>Ingresa el correo electrónico que registraste con nosotros en tu compra o última visita.</p>  
                            : null}
                            {preferencia === "VIN" ? 
                              <p className="texto-gris" style={{fontSize: 12}}>El número de serie lo podrás encontrar en tu tarjeta de circulación, en la póliza de garantía o en la placa de identificación del vehículo. <a onClick={() => {setError(12); setModal(true)}}>Ver imagen de ejemplo</a></p>  
                            : null}
                          </Col>
                        </Row>
                        <Button ref={refForm} onClick={() => continuar()} style={{display: 'none'}}></Button>
                        {show === true ? 
                            <center className="pt-5">
                            <Keyboards options={optionsKeyboard} setOptionsKeyboard={setOptionsKeyboard} optionsKeyboard={optionsKeyboard}/>
                            </center>
                        : null }
                    </div>
                </Row>
                <Row className="row-cita-abajo">
                    <VideoSeccion refForm={refForm} none="tiene"/>
                </Row>
            </Container>
            <ModalEmergente modal={modal} error={error} setModal={setModal} registro={true} setClick={setClick} mensajeModal={mensajeModal}/>
        </Layout>
    )

}